<template>
<div>
  <div id="map" :style="MapStyle"></div>
  
  <div class="map-control">
    <!-- WMTS Layer 选择下拉列表 -->
    <div id="wmts-layer-selector">
      <el-select filterable v-model="selectedWMTSLayer" @change="onSelectWMTSLayerChange">
        <el-option v-for="oneLayer in WMTSLayers" :key="oneLayer.Identifier"
        :value="oneLayer.Identifier" :label="oneLayer.Title"></el-option>
      </el-select>
    </div>
  </div>
</div>
</template>

<script>
// 基础模块
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';

// 导入加载地图服务资源的模块
import WMTS, {optionsFromCapabilities} from 'ol/source/WMTS';
import WMTSCapabilities from 'ol/format/WMTSCapabilities';

// 控件
import Control from 'ol/control/Control';
import {defaults as defaultControls} from 'ol/control.js';

// 工具库
import $ from 'jquery';
import echarts from 'echarts';
// import EChartsLayer from 'ol-echarts';

import bus from '../components/bus';

export default {
  data() {
    return {
      map: {},
      view: {},
      layers: [],
      events: [
        {
          eventName: '十五岁的李清照到溪亭游玩',
          time: 1098,
          reignTitle: '元符元年',
          placeString: '溪亭（章丘附近）',
          placeCoordinate: [117.53, 36.72], // EPSG:4326(WGS84)，规定东经为正，北纬为正
        },{
          eventName: '十六岁的李清照随父亲李格非由原籍赴汴京，结识文学上的忘年交晁补之',
          time: 1099,
          reignTitle: '元符二年',
          placeString: '汴京（今河南省开封市）',
          placeCoordinate: [114.35, 34.79],
        }
      ],
      myChart: {},
      MapStyle: {
        width: '100%',
        height: '100%'
      },
      WMTSResults: {},
      WMTSLayers: [{
        Title: '',
        Identifier: ''
      }],
      selectedWMTSLayer: '南宋歷史地圖(S. Song)'
    }
  }, // data
  methods: {
    preDefine() {
      // view
      this.view = new View({
        center: [12175093.67465372, 4209022.808896985],
        zoom: 4.5,
        projection: 'EPSG:3857',
      });

      // layers
      this.layers = [
        new TileLayer({
          source: new OSM(),
          opacity: 0.7
        })
      ];
    },
    createMap() {
      this.map = new Map({
        target: 'map',
        view: this.view,
        layers: this.layers
      });

      this.map.on('click', event => {
        console.log('点击坐标：(' + event.coordinate.toString() + ')');
        console.log('地图缩放：' + event.map.getView().getZoom());
      });
    },
    addWMTSMap() {
      // 创建解析器
      let parser = new WMTSCapabilities();

      // 获取WMTS数据
      fetch('http://gis.sinica.edu.tw/ccts/wmts/1.0.0/WMTSCapabilities.xml')
      .then( response => {
        return response.text();
      }).then( text => {
        // 解析WMTS数据
        let result = parser.read(text); // 对结果进行解析
        this.WMTSResults = result;

        let options = optionsFromCapabilities(result, {
          layer: 'ad1111',
          matrixSet: 'GoogleMapsCompatible'
        });

        // 添加到map图层中
        this.map.addLayer(
          new TileLayer({
            opacity: 0.9,
            source: new WMTS(options)
          })
        );

        // 进一步利用数据
        let layers = result.Contents.Layer; // 获取图层（Title：图层名称；Identifier：图层标识码）
        this.WMTSLayers = layers;

        let layerNames = layers.map( layer => {
          return layer.Title
        });
        // console.log(layerNames);
      });
    },
    onSelectWMTSLayerChange(Identifier) {
      const mapLayers = this.map.getLayers();
      const result = this.WMTSResults;

      let options = optionsFromCapabilities(result, {
        layer: Identifier,
        matrixSet: 'GoogleMapsCompatible'
      });

      // 删除原有图层
      mapLayers.pop();

      // 添加新的图层
      mapLayers.push(
        new TileLayer({
          opacity: 1,
          source: new WMTS(options)
        })
      );
    },
    addControl() {
      this.map.addControl(new Control({
        element: $('#wmts-layer-selector')[0]
      }));
    },
    sendHttpRequest() {
      
    }
  }, // methods
  created() {
    const self = this;

    // 预设地图高度
    // const HeaderHeight = 50;
    // self.MapStyle.height = window.innerHeight - HeaderHeight + 'px';

    // 创建地图高度随窗口变化的事件
    // bus.$off('mapResize');
    // bus.$on('mapResize', win => {
    //   let screenHeight = win.innerHeight;
    //   self.MapStyle.height = screenHeight - HeaderHeight + 'px';
    // });
  },
  mounted() {
    const self = this;
    // 创建地图
    self.preDefine();
    self.createMap();

    // 添加图层
    self.addWMTSMap();

    // 添加控件
    self.addControl();

    // 发送 http 请求调用名人数据库
    self.sendHttpRequest();
  }, // mounted
}
</script>

<style scoped>
#wmts-layer-selector {
  position: absolute;
  right: 25px;
  top: 10px;
}
</style>